import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,

  name: PropTypes.string.isRequired,
  address: PropTypes.string,
  city: PropTypes.string,
  postalCode: PropTypes.string,
  notes: PropTypes.string,
  createdDate: PropTypes.string.isRequired,
});

export const formValues = PropTypes.shape({
  name: PropTypes.string,
  address: PropTypes.string,
  city: PropTypes.string,
  postalCode: PropTypes.string,
  notes: PropTypes.string,
  createdDate: PropTypes.oneOfType([PropTypes.string, PropTypes.instanceOf(Date)]),
});

export const formTouched = PropTypes.shape({
  name: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  address: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  city: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  postalCode: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  notes: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  createdDate: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  name: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  address: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  city: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  postalCode: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  notes: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  createdDate: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
