import { getDefaultOptions, request } from 'api/helpers';

const resource = 'locations';

export const apiLocationGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiLocationPost = async (serviceUrl, location) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: location ? JSON.stringify(location) : null,
  };
  return request(url, options);
};

export const apiLocationPut = async (serviceUrl, location) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: location ? JSON.stringify(location) : null,
  };
  return request(url, options);
};

export const apiLocationDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
